const { Icon } = wp.components;

const CategoryIcon = ({ category }) => {
	if (category?.icon && typeof category?.icon === 'string' && category.icon.includes('http')) {
		return <img className='conditional-blocks-category-icon' src={category.icon} alt='' />;
	} else if (category?.icon && typeof category?.icon === 'string') {
		return (
			<span className='conditional-blocks-category-icon'>
				<Icon icon={category.icon} />
			</span>
		);
	} else {
		return (
			<span className='conditional-blocks-category-icon'>
				<Icon icon='admin-plugins' />
			</span>
		);
	}
};

export default CategoryIcon;
